import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow
} from '@mui/material';



const WeeklyAvailableFriends = ({ weekData }) => {

  const columnHeader = [
    { id: 'friends', lable: 'Friends', minWidth: 30 },
    { id: 'availability', lable: 'Availability', minWidth: 30 },
    { id: 'availableDays', lable: 'Available Days', minWidth: 130 },
  ];

  return(
    <TableContainer sx={{ maxHeight: 600 }}>
      <Table stickyHeader aria-label='sticky table'>
        <TableHead>
          <TableRow>
            {columnHeader.map((column) => (
              <TableCell
                key={column.id}
                align='left'
                style={{ minWidth: column.minWidth }}
                sx={{ backgroundColor: '#1e556b', color: 'black' , fontSize: '18px' }}
              >
                {column.lable}
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {weekData.friendsAvailability.map((friend) => (
            <TableRow hover role='checkbox' tabIndex={-1} key={friend.friends.id}>
              <TableCell align='left' >
                {friend.friends.firstname}
              </TableCell>
              <TableCell align='left' >
                week {weekData.weekNo}
              </TableCell>
              <TableCell align='left' >
                {friend.matchDays? friend.matchDays.join(',  ') : '' }
              </TableCell>
            </TableRow>
          ))
          }

        </TableBody>
      </Table>
    </TableContainer>

  );
};

export default WeeklyAvailableFriends;