import React,{ useState } from 'react';
import  { useQuery } from 'react-query';

import {
  //Box,
  Autocomplete,
  TextField,
  //Typography,
} from '@mui/material';

import userServices from '../services/userServices';
import UserProfile from './UserProfile';

const Users = () => {

  // selected user
  const [selectedUser, setSelectedUser] = useState(null);

  // read users
  const userResult = useQuery('users', userServices.getAllUsers, {
    refetchOnWindowFocus: false,
  });

  if (userResult.isLoading) return <h1>Loading...</h1>;
  if (userResult.error) return <h1>Error</h1>;

  const users = userResult.data.users;

  // return null if users does not exist
  if (!users) return null;

  return (
    <div>
      <h1>Users</h1>
      <h2>Selected User : {selectedUser ? `${selectedUser.firstname}  ${selectedUser.lastname}` : '' }</h2>
      <Autocomplete
        disablePortal
        id='users'
        options={users}
        getOptionLabel={(option) => `${option.firstname} ${option.lastname}`}
        onChange={(e, value) => setSelectedUser(value)}
        renderInput={(params) => <TextField {...params} label='Users' />}
        size='small'
        sx={{ maxWidth: '350px', margin: '2' }}
        value={selectedUser}
      />
      <UserProfile user={selectedUser} />
    </div>
  );
};

export default Users;
