import { useState } from 'react';

import {
  Box,
  Button,
  Checkbox,
  FormControlLabel,
  Grid,
  Typography,
} from '@mui/material';

const WeekAvailablity = ({ weekData, handlechangeAvailability, clearWeekAvailability }) => {

  const weekNo = weekData.weekNo;
  const initialAvailability = weekData.availableDays;

  // initial week availability
  //const initialWeekAvailability = Object.values(initialAvailability).some((day) => day);
  const [weekAvailability, setWeekAvailability] = useState(true);

  // clear availability for whole week
  const handleClearWeekAvailability = () => {

    // check is the week Availability is true
    if (weekAvailability) {
      clearWeekAvailability(weekNo);
    }
    setWeekAvailability(!weekAvailability);
  };

  return (
    <Box border={'solid'} margin={1}>
      <Grid container flex={10} direction={'row'} margin={1} >
        <Typography variant='h3' marginLeft={2} marginRight={2}> Week {weekNo}</Typography>
        <Checkbox
          size='large'
          sx={{ marginRight: 3 }}
          checked={weekAvailability}
          onChange={handleClearWeekAvailability}
        />
        {Object.keys(initialAvailability).map((day) => (
          <FormControlLabel
            sx={{ margin: '0px' }}
            key={day}
            control={
              <Button
                variant='contained'
                size='small'
                sx={{ width: '150px', margin: '0px', fontSize: '20px',   }}
                color={initialAvailability[day] ? 'success' : 'inherit'}
                onClick={() => handlechangeAvailability(weekNo, day)}
              >
                {day}
              </Button>
            }
          />
        ))}
      </Grid>
    </Box>
  );
};

export default WeekAvailablity;