import { useEffect, useState } from 'react';
import { useQuery, useMutation, useQueryClient } from 'react-query';

import {
  Box,
  Button,
  Grid,
  Typography
} from '@mui/material';

import userServices from '../services/userServices';
import WeekAvailablity from './WeekAvailability';

const UserAvailability = ({ user, thisWeek }) => {

  const [weekAvailability, setWeekAvailability] = useState([]);

  const queryClient = useQueryClient();


  const updateUserAvailabilities = useMutation(userServices.setUserAvailability,{
    onSuccess: () => {
      queryClient.invalidateQueries('userAvailabilities');
    },
    onError: () => {
      console.log('error un update availabilities');
    }
  });

  // read user availability
  const availabilityResult = useQuery(['userAvailabilities', user._id], () => userServices.getUserAvailability(user._id, thisWeek), {
    refetchOnWindowFocus: false,
    enabled: user._id ? true : false
  });

  let userAvailabilities = [];
  useEffect(() => {
    if(userAvailabilities && userAvailabilities.length === 7 ) {
      setWeekAvailability(userAvailabilities);
    }
  },[availabilityResult.data,user]);

  if (availabilityResult.isLoading) return <h1>Loading...</h1>;
  if (availabilityResult.error) return <h1>Error</h1>;


  let availability = availabilityResult.data.availability.userAvailabilities;
  // set weekly availability
  for (let week = thisWeek; week < thisWeek + 7; week++) {
    // extract available days form user data on the server
    let weekAvailableDays = availability.filter(a => a.weekNo === week);

    let weekAvailability = {
      weekNo: week,
      availableDays: {
        monday: weekAvailableDays[0]? weekAvailableDays[0].availableDays.includes('monday') : false,
        tuesday: weekAvailableDays[0]? weekAvailableDays[0].availableDays.includes('tuesday') : false,
        wednesday: weekAvailableDays[0]? weekAvailableDays[0].availableDays.includes('wednesday') : false,
        thursday:  weekAvailableDays[0]? weekAvailableDays[0].availableDays.includes('thursday') : false,
        friday:  weekAvailableDays[0]? weekAvailableDays[0].availableDays.includes('friday') : false,
        saturday:  weekAvailableDays[0]? weekAvailableDays[0].availableDays.includes('saturday') : false,
        sunday: weekAvailableDays[0]? weekAvailableDays[0].availableDays.includes('sunday') : false,
      }
    };
    userAvailabilities.push(weekAvailability);
  }

  const changeAvailability = (weekNo, day) => {
    // find updated week
    const updatedAvailability = weekAvailability.find(week => week.weekNo === weekNo);
    // change the availability
    updatedAvailability.availableDays[day] = !updatedAvailability.availableDays[day];
    // update the availability
    const UserAvailability = weekAvailability.map(week => week.weekNo === weekNo ? updatedAvailability : week);
    setWeekAvailability(UserAvailability);
  };

  // clear week availability
  const clearWeekAvailability = (weekNo) => {
    // find updated week
    const updatedAvailability = weekAvailability.find(week => week.weekNo === weekNo);
    // clear availability for whole week
    ['monday', 'tuesday', 'wednesday', 'thursday', 'friday', 'saturday', 'sunday'].forEach((day) => {
      updatedAvailability.availableDays[day] = false;
    });
    // update the availability
    const UserAvailability = weekAvailability.map(week => week.weekNo === weekNo ? updatedAvailability : week);
    setWeekAvailability(UserAvailability);


  };


  // Handle Save Availability
  const handleSaveAvailability = () => {
    // read the updated availability
    const updatedAvailability = weekAvailability.map(week => {
      return {
        weekNo: week.weekNo,
        availableDays: Object.keys(week.availableDays).filter(day => week.availableDays[day])
      };
    });

    updateUserAvailabilities.mutate({ user:user._id, availability: updatedAvailability });
  };

  return (
    <Box>
      <Typography variant='h4' color={'#ef7238'}>
        MY AVAILABILITY FOR THE NEXT 7 WEEKS
      </Typography>
      {weekAvailability.map(week => (
        <WeekAvailablity
          key={week.weekNo}
          weekData={week}
          handlechangeAvailability={changeAvailability}
          clearWeekAvailability={clearWeekAvailability}
        />
      ))}
      <Grid container alignItems='center' justifyContent='flex-start'>
        <Grid item>
          <Button
            variant='contained'
            color='primary'
            size='large'
            onClick={handleSaveAvailability}
            sx={{ margin: '30px', borderRadius: '30px', fontSize: '20px'    }}
          >
            SAVE
          </Button>
        </Grid>
      </Grid>
    </Box>
  );
};

export default UserAvailability;