import { useState } from 'react';

import {
  Box,
  Button,
  Grid,
  Typography
} from '@mui/material';
import { useQuery } from 'react-query';
import userServices from '../services/userServices';
import WeeklyAvailableFriends from './WeeklyAvailableFriends';


const UserFriendsAvailability = ({ user, weekNo }) => {

  const [selectedWeek, setSelectedWeek] = useState(null);

  // fetch friends availability
  const friendsAvailability = useQuery(['friendsAvailability', user._id], () => userServices.getFriendsAvailability(user._id, weekNo), {
    refetchOnWindowFocus: false,
    enabled: user._id ? true : false
  });

  if (friendsAvailability.isLoading) return <h1>Loading...</h1>;
  if (friendsAvailability.error) return <h1>Error</h1>;

  if (!friendsAvailability.data) {
    <h1>Loading...</h1>;
  }

  // extract match friends for this week
  const thisWeekFilter = friendsAvailability.data.availability.filter(fa => fa.weekNo === weekNo)[0];
  const thisWeekMatch = {
    weekNo: weekNo,
    friendsAvailability: thisWeekFilter ? thisWeekFilter.friendsAvailability.filter(fa => fa.matchDays.length > 0) : []
  };

  // extract match friends for next week
  const nextWeekFilter = friendsAvailability.data.availability.filter(fa => fa.weekNo === weekNo + 1)[0];
  const nextWeekMatch = {
    weekNo: weekNo + 1,
    friendsAvailability: nextWeekFilter ? nextWeekFilter.friendsAvailability.filter(fa => fa.matchDays.length > 0) : []
  };

  // extract match friends for best week
  // fileter olny friends with same available day
  const allWeeksWithMatch = friendsAvailability.data.availability.map(entry => {
    if (entry.friendsAvailability) {
      const filteredFriendsAvailability = entry.friendsAvailability.filter(item => item.matchDays.length > 0);
      return { ...entry, friendsAvailability: filteredFriendsAvailability };
    }
    return entry;
  });

  const maxFriendsAvailability = allWeeksWithMatch.reduce((maxEntry, currentEntry) => {
    if (currentEntry.friendsAvailability && currentEntry.friendsAvailability.length > (maxEntry.friendsAvailability? maxEntry.friendsAvailability.length : 0)) {
      return currentEntry;
    }
    return maxEntry;
  }, {});
  // claculate max friends availability
  const bestWeek = maxFriendsAvailability.friendsAvailability ? maxFriendsAvailability.friendsAvailability.length : 0;

  // extract all friends with as least one match available day
  const allAvailableFriendsObject = friendsAvailability.data.availability.reduce((result, entry) => {
    entry.friendsAvailability.forEach(friend => {
      const { friends, matchDays } = friend;
      if (matchDays.length > 0) {
        if (!result[friends.id]) {
          result[friends.id] = { friends : { firstname: friends.firstname,
            lastname: friends.lastname,
            id: friends.id
          },
          availability: [{ weekNo: entry.weekNo, matchDays }],
          matchDays:[`week ${entry.weekNo}`]
          };
        } else {
          result[friends.id].availability.push({ weekNo: entry.weekNo, matchDays });
          result[friends.id].matchDays.push(`week ${entry.weekNo}`);
        }
      }
    });
    return result;
  },{});

  // convert object to array
  const allAvailableFriends = Object.values(allAvailableFriendsObject);
  // convert to weekly data type
  const convertedData = {
    weekNo: `${weekNo} - ${weekNo+6} `,
    friendsAvailability: allAvailableFriends
  };
  return(
    <Box marginBottom={30}>
      <Typography variant='h4' color={'#ef7238'}>
        {user && user.firstname && user.firstname.toUpperCase()}&apos;S FRIENDS
      </Typography>
      <Box border={'solid'} sx={{ height: '100px' }}>
        <Grid container direction={'row'} justifyContent={'space-between'}>
          <Typography variant='body1' margin={3}>
            availability
          </Typography>
          <Button
            onClick={() => setSelectedWeek(convertedData)}
            variant='text'
            color='inherit'
          >
            <Typography variant='body1' margin={3}>
            All weeks({allAvailableFriends.length})
            </Typography>
          </Button>
          <Button
            onClick={() => setSelectedWeek(thisWeekMatch)}
            variant='text'
            color='inherit'
          >
            <Typography variant='body1' margin={3}>
              This Week({thisWeekMatch.friendsAvailability.length})
            </Typography>
          </Button>
          <Button
            onClick={() => setSelectedWeek(nextWeekMatch)}
            variant='text'
            color='inherit'
          >
            <Typography variant='body1' margin={3}>
              Next Week({nextWeekMatch.friendsAvailability.length})
            </Typography>
          </Button>
          <Button
            onClick={() => setSelectedWeek(maxFriendsAvailability)}
            variant='text'
            color='inherit'
          >
            <Typography variant='body1' margin={3}>
            Best Week({bestWeek})
            </Typography>
          </Button>
        </Grid>
      </Box>
      {selectedWeek ? <WeeklyAvailableFriends weekData={selectedWeek} /> : null}
    </Box>
  );

};

export default UserFriendsAvailability;