import React from 'react';
import UserAvailability from './UserAvailability';

import {
  //Box,
  Grid,
  Paper,
  Typography,
} from '@mui/material';

import UserFriendsAvailability from './UserFriendsAvailability';
import calculateWeekNo from '../utils/calculateWeekNo';

const UserProfile = ({ user }) => {
  if (!user) return null;

  // read Date andcalculate week No.
  const date = new Date();
  const thisWeek = calculateWeekNo(date);

  return (
    <Paper elevation={3} sx={{ maxWidth: '1400px' }} >
      <Grid textAlign={'center'} flex={12}>
        <Typography variant='h2'>{user.firstname} {user.lastname}</Typography>
        <Typography variant='h5'>Number of friends: {user.friends.length}</Typography>
        <UserFriendsAvailability user={user} weekNo={thisWeek} />
        <UserAvailability user={user} thisWeek={thisWeek} />
      </Grid>
    </Paper>
  );
};

export default UserProfile;
