import axios from 'axios';

const baseUrl = '/api/users/';

// get all users
const getAllUsers = async () => {
  const res = await axios.get(baseUrl);
  console.log(' service user result', res);
  return res.data;
};

// get user availabilities
const getUserAvailability = async (user, weekNo) => {

  const res = await axios.get(`${baseUrl}/availability/${user}`, {
    params: {
      weekNo: weekNo
    } }
  );
  return res.data;
};

// set user availabilities
const setUserAvailability = async ({ user, availability }) => {

  const res = await axios.put(`${baseUrl}/availability/${user}`, { availability });
  console.log(' user service * availability ->',res.data);
  return res.data;
};


// read friends availabilities
const getFriendsAvailability = async (user, weekNo) => {
  const res = await axios.get(`${baseUrl}/friends/availability/${user}`, {
    params: {
      weekNo: weekNo
    }
  });
  return res.data;
};



export default {
  getAllUsers,
  getUserAvailability,
  setUserAvailability,
  getFriendsAvailability
};